body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.bd_posts {
	padding: 1rem;
}

.bd_posts h1 {
	font-weight: normal;
	text-transform: capitalize;
}

.bg-vw--bx {
    max-width: calc(100% - 10px);
    min-width: 600px;
}

.bg-vw {
    margin: auto;
    width: 100%;
    border-top: thin solid #ddd;
    padding: 1.8rem 0;
}

.bg-vw:nth-child(1) {
    border-top: none;
}

.bg-vw header {
    font-size: 25px;
    font-weight: 400;
}

.bg-vw span {
  color: #898989;
}

.bg-vw a {
  text-decoration: none;
  color: #000;
  color: initial;
}

.bg-dt--bx {
    width: 100%;
}

.bg-dt {
    margin: auto;
    width: 100%;
}

.mk-post {
	position: fixed;
	bottom: 20px;
	right: 20px;
	width: 60px;
	height: 60px;
	border-radius: 50%;
	box-shadow: 1px 1px 3px #ddd, 3px 3px 5px #eee;
	display: flex;
	justify-content: center;
	align-items: center;
	transition: ease-in-out .3s;
	color: teal;
	background: transparent;
	z-index: 3;
}

.mk-post div {
	position: absolute;
	top: 0;
	right: 0;
	left: 0;
	bottom: 0;
	background-color: red;
	z-index: -1;
	border-radius: 50%;
	background: teal;
	-webkit-clip-path: circle(0 at 0);
	        clip-path: circle(0 at 0);
	transition: ease-in-out .3s;
}

.mk-post:hover {
	color: white;
}

.mk-post:hover div {
	-webkit-clip-path: circle(100% at 0);
	        clip-path: circle(100% at 0);
}
